.form {
    display: -ms-flexbox;
    display: flex;
}

.form input {
    -ms-flex: 1 1;
        flex: 1 1;
    font-size: 1.25rem;
    outline: none;
    border: none;
    border-bottom: 1px solid #c5f6fa;
}

.create-button {
    padding: 0.5rem 1rem;
    margin-left: 1rem;
    background: #22b8cf;
    border-radius: 3px;
    color: white;
    font-weight: 600;
    cursor: pointer;
}

.create-button:hover {
    background: #3bc9db;
}


